@font-face {
  font-family: 'sell-icon';
  src:  url('../fonts/sell-icon.eot?1vt050');
  src:  url('../fonts/sell-icon.eot?1vt050#iefix') format('embedded-opentype'),
    url('../fonts/sell-icon.ttf?1vt050') format('truetype'),
    url('../fonts/sell-icon.woff?1vt050') format('woff'),
    url('../fonts/sell-icon.svg?1vt050#sell-icon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'sell-icon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-undo:before {
  content: "\e965";
}
.icon-redo:before {
  content: "\e966";
}



